import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';

import { Link } from "react-router-dom";

import Detail from './Detail';

const styles = theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)' 
  }
});

class ProductList extends Component {
  constructor(props) {
    super(props);
  }

  detailLink(product) {
    switch (product.category) {
      case 'body':
        return process.env.PUBLIC_URL +'/bodies/' + product.id; 
      default:
        return process.env.PUBLIC_URL + '/products/' + product.id;
    }
  }

  render() {
    const { products, classes } = this.props;

    return (
      <div className={classes.root}>
        <GridList cellHeight={160} cols={3} cellHeight={340}>
          { products.map( product => {
            
            return (
            <GridListTile 
              key={product.image} 
              cols={product.cols || 1}
              >
              <img src={product.image} alt={product.image} />
              
              <GridListTileBar
                title={product.category}
                subtitle={product.desc}
                actionIcon={
                  <IconButton className={classes.icon} component={Link} to={this.detailLink(product)}>
                    <InfoIcon />
                  </IconButton>
                }
              />
            </GridListTile>);
            
          })}
        </GridList>
      </div>
   );
  }
}

ProductList.propTypes = {
  products: PropTypes.array.isRequired
}

export default withStyles(styles)(ProductList);



