import React, { Component } from 'react';
import AppBar from './AppBar';
import ProductList from './ProductList';
import Detail from './Detail';
import DetailBody from './DetailBody';

import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      products : []   
    };
  }

  componentWillMount() {
    fetch(process.env.PUBLIC_URL + "/api/productlist") 
      .then(res => res.json())
      .then((result) => {
        this.setState({products: result.data}) 
      })
  }

  render() {
    const productList = () => <ProductList products={this.state.products} />;
    const noMatch = ({ location }) => 
      <div> 
        Leider ist unter der Addresse <code>{location.pathname} </code> 
        nichts zu sehen. 
      </div>;

    return (
      <Router>
        <div>
          <AppBar />
          <Switch>
            <Route path={ process.env.PUBLIC_URL + '/'} component={ productList } exact />
            <Route path={ process.env.PUBLIC_URL + '/products/:id'} component={Detail} /> 
            <Route path={ process.env.PUBLIC_URL + '/bodies/:id'} component={DetailBody} /> 
            <Route component={noMatch} />
          </Switch> </div>
      </Router>
    );
  }
}

export default App;



